<template>
  <div class="livescore">
    <div class="leftcol">
      <calendar></calendar>
      <div class="container">
        <div class="contentLivescore">
          <div class="inplay">
            <div @click="opendetail()">
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
              <span>test</span><br>
            </div>
          </div>
          <div class="pregame">
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test</span><br>
            <span>test123</span><br>
          </div>
        </div>
        <div class="footerPrediction">
          <span>All Right Reserved. © 2018. Powered by In-Play</span>
        </div>
      </div>
    </div>
    <div class="rightcol" :class="{'showDetailPanel':ishowDetailLivescore}">
      <div class="detailPanel" @click="close()">
        detail
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import calendar from "./livescore/calendar";
export default {
  components: {
    calendar
  },
  computed: {
    ...mapGetters(["ishowDetailLivescore"])
  },
  methods: {
    opendetail() {
      this.$store.commit("setishowDetailLivescore", true);
    },
    close() {
      this.$store.commit("setishowDetailLivescore", false);
    }
  }
};
</script>
<style lang="scss" scoped>
@media (min-width: 320px) {
  .leftcol {
    width: 100%;
  }
  .rightcol {
    width: 100%;
    position: fixed;
    z-index: 3;
    top: 0;
  }
  .contentLivescore {
    flex-direction: column;
    align-items: center;
    margin-right: 0px;
  }
}
@media (min-width: 672px) {
  .leftcol {
    position: unset;
    width: 352px;
  }
  .rightcol {
    position: unset;
    flex: 1;
    z-index: 1;
  }
  .container {
    margin-right: 8px;
    margin-left: 8px;
  }
  .contentLivescore {
    margin-right: 8px;
  }
}
@media (min-width: 865px) {
  .leftcol {
    flex: 1;
  }
  .rightcol {
    flex: none;
    width: 512px;
  }
}
@media (min-width: 1248px) {
  .contentPrediction {
    flex-direction: unset;
    align-items: unset;
    justify-content: center;
  }
  .inplay {
    margin-right: 8px;
  }
}
.livescore {
  background-color: #333;
  height: 100%;
  display: flex;
  width: 100%;
  position: absolute;
  top: 64px;
}
.leftcol {
  background-color: #333;
  height: 100%;
}
.rightcol {
  background-color: rgba(0, 0, 0, 0.5);
  transform: translateX(100%);
  transition: transform 0.5s linear;
  height: 100%;
}
.showDetailPanel {
  transform: translateX(0) !important;
}
.disable {
  visibility: hidden;
}
.detailPanel {
  background-color: #fff;
  height: 100%;
  width: 100%;
  position: relative;
  max-width: 512px;
  min-width: 320px;
  float: right;
}
.calendar {
  height: 62px;
  background-color: #212121;
}
.container {
  height: 100%;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  min-height: calc(100% - 126px);
  max-height: calc(100% - 126px);
}
.contentLivescore {
  display: flex;
  padding-top: 16px;
  min-height: calc(100% - 81px);
}
.inplay {
  background-color: teal;
  max-width: 360px;
  min-width: 320px;
  width: 100%;
}
.pregame {
  background-color: antiquewhite;
  max-width: 360px;
  min-width: 320px;
  width: 100%;
}
.footerPrediction {
  background-color: #333;
  height: 64px;
  box-shadow: 0 -1px 0 0 hsla(0, 0%, 100%, 0.1);
  color: #fff;
  opacity: 0.34;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  margin-top: 16px;
}

</style>





